.btn{
      padding: 4px 8px;
      border-radius: 8px;
      color: white;
      background-color: rgb(193, 183, 193);
      cursor: pointer;
      &:hover {
        background-color: rgb(155, 155, 169);
      }
}

@media screen and (min-width: 320px){
  .btn{
        padding: 2px 4px;
        border-radius: 8px;
        color: white;
        display: flex;
        justify-content: center;
      }
  }
